<code>#include&lt;iostream&gt;
using namespace std;

void my_swap ( int  x, int y )
{
    int  temp = x;
    x = y;
    y = temp;
    cout&lt;&lt;"x="&lt;&lt;x&lt;&lt;" y="&lt;&lt;y&lt;&lt;endl;
}
int main (  )
{
    int a, b;
    cin&gt;&gt;a&gt;&gt;b;
    my_swap(a, b);
    cout&lt;&lt;"a="&lt;&lt;a&lt;&lt;" b="&lt;&lt;b&lt;&lt;endl;
}</code></pre>
