 <pre class="ne-codeblock language-cpp" data-language="cpp" id="oWpTV"><span style="font-weight:bold;">2.1</span> 
<code>#include&lt;iostream&gt;
using namespace std;

void overload_swap ( int  &amp;x, int &amp;y)	//
{
    swap(x,y);
    cout&lt;&lt;"swap 2 integers"&lt;&lt;endl;
}
void overload_swap ( string  &amp;x, string &amp;y)	//
{
    swap(x,y);
    cout&lt;&lt;"swap 2 strings"&lt;&lt;endl;
}
void overload_swap ( int  &amp;x, int &amp;y, int &amp;z)	//
{
    swap(z,y);
    swap(x,y);
    swap(y,z);
    cout&lt;&lt;"swap 3 integers"&lt;&lt;endl;
}
int main (  )
{
    int a, b, c;
    cin&gt;&gt;a&gt;&gt;b;
    overload_swap(a, b);
    cout&lt;&lt;"a="&lt;&lt;a&lt;&lt;" b="&lt;&lt;b&lt;&lt;endl;
    string s1,s2;
    cin&gt;&gt;s1&gt;&gt;s2;
    overload_swap(s1, s2);
    cout&lt;&lt;"s1="&lt;&lt;s1&lt;&lt;" s2="&lt;&lt;s2&lt;&lt;endl;
    cin&gt;&gt;a&gt;&gt;b&gt;&gt;c;
    overload_swap(a, b, c);
    cout&lt;&lt;"a="&lt;&lt;a&lt;&lt;" b="&lt;&lt;b&lt;&lt;" c="&lt;&lt;c&lt;&lt;endl;
}</code></pre>
