}
int main (  )
{
    int a=3;
    double b=1.2;
    char c='A';
    long long d=4;
    float e=8.7;
    dummy();
    cout&lt;&lt;"a("&lt;&lt;a&lt;&lt;")\t"&lt;&lt;(&amp;a)&lt;&lt;""&lt;&lt;sizeof(a)&lt;&lt;""&lt;&lt;endl;
    cout&lt;&lt;"b("&lt;&lt;b&lt;&lt;")\t"&lt;&lt;(&amp;b)&lt;&lt;""&lt;&lt;sizeof(b)&lt;&lt;""&lt;&lt;endl;
    cout&lt;&lt;"c("&lt;&lt;c&lt;&lt;")\t"&lt;&lt;(void*)(&amp;c)&lt;&lt;""&lt;&lt;sizeof(c)&lt;&lt;""&lt;&lt;endl;
    cout&lt;&lt;"d("&lt;&lt;d&lt;&lt;")\t"&lt;&lt;(&amp;d)&lt;&lt;""&lt;&lt;sizeof(d)&lt;&lt;""&lt;&lt;endl;
